/* Import for the font Hack. */
@import url("https://cdn.jsdelivr.net/npm/@xz/fonts@1/serve/hack.min.css");

/* Define custom properties for colors. */
:root {
  --black: #282c34;
  --white: white;
  --light-gray: #e6e6e6;
  --gray: #adadad;
  --dark-gray: #b5b4b4;
  --orange: #f79256;
  --blue: #61dafb;
  --light-blue: #b0ddea;
  --translate-blue: #3399ff;
  --dialogue-dark-blue: #6daffe;
  --dialogue-light-blue: #edf6ff;
}

html {
  /* Set font-size of root element in order to use rem for other elements and make those relative to the root font-size. */
  font-size: 16px;
}

body {
  margin: 0;
  font-family: Menlo, "Hack", monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

/* Override default styling for native HTML buttons. */
button {
  font-family: Menlo, "Hack", monospace;
  color: var(--white);
  padding: 0;
  margin: 0;
  border: hidden;
  width: 100%;
  background: none;
  outline: none;
}

/* Override default styling for native HTML input. */
input {
  border: none;
  outline: none;
  margin: 0;
  padding: 0;
  background-color: transparent;
  font-size: 0.85rem;
  font-family: Menlo, "Hack", monospace;
}

/* Global Icon Grid Styling. Applies to AppFileIconGrid and SourcesWinBoxContent */
.grid {
  margin-top: 0.8rem;

  /* Arrange icons in gridbox. */
  display: grid;
  column-gap: 0.5rem;
}

.grid-icon {
  font-size: 0.75rem;

  /* Center icon and icon name vertically. */
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;

  /* Make icon flush to top of flexbox column
     and icon name flush to bottom of flexbox column
     so icons are aligned at top and icon names at bottom. */
  justify-content: space-between;
  height: 100%;
}

/* Global Icon Grid Styling Inside WinBoxes. */
.custom-winbox .grid-icon {
  font-size: 0.65rem;
  color: var(--black);
  text-decoration: none;
}

/* Global Text Paragraph Styling. */
.text p {
  text-align: justify;
  text-justify: auto;
}
